import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage-angular';

@Injectable({ providedIn: 'root' })
export class StorageService {
  private mainStorage: Storage;

  constructor(private storage: Storage) {}

  async init() {
    const storage = await this.storage;
    this.mainStorage = storage;
    this.mainStorage.create();
  }

  async set(key: string, value: any) {
    await this.mainStorage.set(key, value);
  }

  async setClear() {
    await this.mainStorage.clear();
  }

  async storageData(key: string) {
    const data = await this.mainStorage.get(key);
    return data;
  }
  async storageBranch() {
    const data = await this.mainStorage.get('branch');
    if (data) {
      return data;
    }
  }
}
