import { Injectable } from '@angular/core';
import {
  HttpEvent,
  HttpInterceptor,
  HttpHandler,
  HttpRequest,
} from '@angular/common/http';
import { Observable, from } from 'rxjs';
import { StorageService } from '../services/storage.service';

@Injectable()
export class AuthInterceptor implements HttpInterceptor {
  constructor(private storageService: StorageService) {}

  intercept(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    return from(this.handleHttp(req, next));
  }

  async handleHttp(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Promise<HttpEvent<any>> {
    const token = await this.storageService.storageData('token');
    const shop = await this.storageService.storageData('shop');
    const branch = await this.storageService.storageData('branch');

    const authReq = req.clone({
      headers: req.headers
        .set('Authorization', 'Bearer ' + token)
        .set('shopId', '' + shop)
        .set('branchId', '' + branch),
    });
    return await next.handle(authReq).toPromise();
  }
}
