import { Injectable } from '@angular/core';
import {
  HttpInterceptor,
  HttpEvent,
  HttpHandler,
  HttpRequest,
  HttpErrorResponse,
  HttpResponse,
} from '@angular/common/http';
import { EMPTY, Observable, from } from 'rxjs';
import { environment } from 'src/environments/environment.prod';
import { ToastController } from '@ionic/angular';
import { catchError, tap } from 'rxjs/operators';
import { AuthService } from '../services/auth.service';

@Injectable()
export class RestInterceptor implements HttpInterceptor {
  constructor(
    private toastCtrl: ToastController,
    private authService: AuthService
  ) {}

  intercept(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    return from(this.handleHttp(req, next));
  }

  async handleHttp(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Promise<HttpEvent<any>> {
    const url = `${environment.baseUrl}/${req.url}`;
    const urlReq = req.clone({
      url,
    });
    const toast = await this.toastCtrl;

    return next
      .handle(urlReq)
      .pipe(
        catchError((error) => {
          if (error instanceof HttpErrorResponse) {
            switch (error.status) {
              case 400:
                toast
                  .create({
                    message: error.error.message,
                    duration: 2000,
                    position: 'bottom',
                    color: 'danger',
                    icon: 'close-circle-outline',
                    buttons: [
                      {
                        icon: 'close',
                        role: 'cancel',
                      },
                    ],
                  })
                  .then((observe) => {
                    observe.present();
                  });
                break;
              case 401:
                toast
                  .create({
                    message: error.error.message,
                    duration: 2000,
                    position: 'bottom',
                    color: 'danger',
                    icon: 'close-circle-outline',
                    buttons: [
                      {
                        icon: 'close',
                        role: 'cancel',
                      },
                    ],
                  })
                  .then((observe) => {
                    observe.present();
                    this.authService.signOut();
                  });

                break;
              case 403:
                toast
                  .create({
                    message: error.error.message,
                    duration: 2000,
                    position: 'bottom',
                    color: 'danger',
                    icon: 'close-circle-outline',
                    buttons: [
                      {
                        icon: 'close',
                        role: 'cancel',
                      },
                    ],
                  })
                  .then((observe) => {
                    observe.present();
                    this.authService.signOut();
                  });

                break;

              default:
                toast
                  .create({
                    message: error.error.message,
                    duration: 2000,
                    position: 'bottom',
                    color: 'danger',
                    icon: 'close-circle-outline',
                    buttons: [
                      {
                        icon: 'close',
                        role: 'cancel',
                      },
                    ],
                  })
                  .then((observe) => {
                    observe.present();
                  });
            }
          }

          return EMPTY;
        }),
        tap((success) => {
          if (success instanceof HttpResponse) {
            const item: HttpEvent<any> = success;
            if (item.body.msg || item.body.message) {
              if (urlReq.method === 'POST') {
                toast
                  .create({
                    message: item.body.msg || item.body.message,
                    duration: 2000,
                    position: 'bottom',
                    color: 'success',
                    icon: 'checkmark-circle-outline',
                  })
                  .then((observe) => {
                    observe.present();
                  });
              } else if (urlReq.method === 'PUT' || urlReq.method === 'PATCH') {
                toast
                  .create({
                    message: item.body.msg || item.body.message,
                    duration: 2000,
                    position: 'bottom',
                    color: 'success',
                    icon: 'checkmark-circle-outline',
                  })
                  .then((observe) => {
                    observe.present();
                  });
              }
            }
          }
        })
      )
      .toPromise();
  }
}
