import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { AuthResponse } from '../models/auth-response.model';
import { StorageService } from './storage.service';
import { appUrl } from '../app-url.module';
import { Branch } from '../models/branch.model';
import { tap } from 'rxjs/operators';

@Injectable({ providedIn: 'root' })
export class AuthService {
  token = '';
  shopId = '';
  branchId = '';
  roles = [''];
  isAuthenticated = false;

  private username!: string;
  private password!: string;

  constructor(
    private http: HttpClient,
    private storageService: StorageService,
    private router: Router
  ) {}

  set setUsername(value: string) {
    this.username = value;
  }
  set setPassword(value: string) {
    this.password = value;
  }

  signIn(): Observable<AuthResponse> {
    return this.http.post<AuthResponse>('login', {
      username: this.username,
      password: this.password,
    });
  }

  async saveAuthData(response: AuthResponse) {
    this.token = response.accessToken;
    this.roles = response.roles;
    this.isAuthenticated = true;

    this.storageService.set('token', this.token);
    this.storageService.set('roles', this.roles);
    this.storageService.set('shopToken', response);

    this.router.navigate([appUrl.home]);
  }

  async saveShop(branch: Branch) {
    await this.storageService.set('shop', branch.shopId);
    await this.storageService.set('branch', branch.id);

    this.router.navigate([appUrl.home]);
  }

  async autoSignIn() {
    // await this.storageService.storageToken();
    // await this.storageService.storageRole();

    const dataToken = await this.storageService.storageData('token');
    const dataRole = await this.storageService.storageData('roles');

    if (dataToken) {
      this.token = dataToken;
      this.roles = dataRole;
      this.isAuthenticated = true;
    }
  }

  async signOut() {
    await this.storageService.setClear();
    this.token = '';
    this.roles = [''];
    this.isAuthenticated = false;

    this.router.navigate([appUrl.login]);
  }
}
